<style scoped lang="less">
  .profit-page {

    .top {
      display: flex;
      align-items: center;
      padding: 10px 0;

      .top-item {
        position: relative;
        top: 0;
        left: 0;
        margin-left: 10px;
      }

      .chooseFile {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }

    .pagination {
      margin-top: 10px;
    }
  }
</style>

<template>
  <div class="profit-page">
    <div class="top">
      <!-- 日期选择器 -->
      <el-date-picker
        class="top-item"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        v-model="time"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>

      <!-- 查询 -->
      <el-button class="top-item" @click="searchTableData">查询</el-button>
      <!-- 导出 -->
      <el-button class="top-item" type="success" @click="exportTableData">导出</el-button>
      <!-- 导入 -->
      <el-button class="top-item" type="primary" v-show="showImportBtn">
        <!-- hide -->
        <input type="file" class="chooseFile" ref="chooseFile"/>
        导入
      </el-button>
    </div>

    <!-- 表格数据 -->
    <el-table
      :data="tableData.data"
      border
      style="width: 100%">
      <el-table-column
        align="center"
        prop="channelId"
        label="渠道号"
        width="180">
      </el-table-column>
      <el-table-column
        align="center"
        prop="endMoney"
        width="180"
        label="结算款">
      </el-table-column>
      <el-table-column
        align="center"
        prop="news"
        width="180"
        label="新增">
      </el-table-column>
      <el-table-column
        align="center"
        prop="info"
        width="180"
        label="信息费">
      </el-table-column>
      <el-table-column
        align="center"
        prop="date"
        width="180"
        label="日期">
      </el-table-column>
    </el-table>
    <!-- 底部按钮 -->
    <el-pagination
      class="pagination"
      @size-change="tableDataSizeChange"
      @current-change="tableDataCurrentChange"
      :current-page="tableData.current_page"
      :page-sizes="[10, 20]"
      :page-size="tableData.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.total">
    </el-pagination>
  </div>
</template>

<script>
import { exportTableData, gameProfitNews, searchGameProfitNews, uploadFile } from '@/utils/api'

export default {
  name: 'profit-page',
  data () {
    return {
      time: '', // 选择日期的时间
      tableData: {
        data: [],
        page_size: 10, // 页面数据个数
        current_page: 1, // 当前 index
        total: 0
      },
      showImportBtn: false
    }
  },

  mounted () {
    // 获取个人信息
    const user = JSON.parse(localStorage.getItem('user'))

    this.showImportBtn = user.loginName === 'admin'

    // 加载表格数据
    this.loadTableData()

    this.$refs.chooseFile.onchange = async (e) => {
      try {
        const res = await uploadFile({ file: e.target.files[0] })
        await this.$checkData(res.data)
      } catch (e) {
        console.log(e)
      }
    }
  },

  methods: {
    // 导出表格数据
    exportTableData () {
      exportTableData()
    },

    // 加载表格数据
    async loadTableData () {
      try {
        const res = await gameProfitNews(this.tableData.current_page, this.tableData.page_size)
        const data = await this.$checkData(res.data)

        console.log(res, data)
        this.updateTableData(data)
      } catch (e) {
        console.log(e)
      }
    },

    // 搜索表格数据
    async searchTableData () {
      const res = await searchGameProfitNews(this.tableData.current_page, this.tableData.page_size, this.time[0], this.time[1])
      const data = await this.$checkData(res.data)

      this.updateTableData(data)
    },

    // 更新表格数据
    updateTableData (data) {
      this.tableData.data = []

      this.tableData.total = data.total
      data.rows.forEach((item) => {
        this.tableData.data.push({
          date: item.recordTime,
          endMoney: item.accountcleark,
          info: item.messageFee,
          news: item.newAdd,
          channelId: item.channelId
        })
      })
    },

    // 分页数据
    tableDataCurrentChange (index) {
      // 记录当前 page
      this.tableData.current_page = index
      this.loadTableData()
    },

    // 一页数据
    tableDataSizeChange (index) {
      this.tableData.page_size = index
      this.loadTableData()
    }
  }
}
</script>
