<style scoped lang="less">
  /*  */
  .el-aside {
    width: 200px;
    background-color: rgb(238, 241, 246);
    color: #333;
  }

  /* 内容主体 */
  .content {
    width: 100%;

    .dataTable {
      margin-top: 20px;
    }

    /* 分页按钮 */
    .pagination {
      margin-top: 20px;
    }

    .userName {
      position: relative;
      top: 0;
      left: 0;
    }

    .userPhone {
      position: relative;
      top: 0;
      left: 0;
    }
  }
</style>

<template>
  <el-tabs class="content" type="border-card" value="Features">
    <el-tab-pane label="功能选择" name="Features">
      <el-row>
        <el-button type="success" @click.stop="newUser">新增</el-button>
      </el-row>
      <!-- 表格数据 -->
      <el-table class="dataTable" :data="featuresInfo.data"
                :border="true">
        <el-table-column width="150" align="center" prop="channelId" label="渠道号">

        </el-table-column>
        <el-table-column width="150" align="center" prop="userName" label="账号">

        </el-table-column>
        <el-table-column width="150" align="center" prop="channelName" label="渠道名称">

        </el-table-column>
        <el-table-column width="200" align="center" prop="date" label="日期">

        </el-table-column>

        <el-table-column width="150" align="center" prop="operate" label="操作选项">
          <template slot-scope="scope">
            <el-button size="mini" type="success" @click="addChannelId(scope.$index)">新增渠道号</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 底部按钮 -->
      <el-pagination
        class="pagination"
        @size-change="featuresSizeChange"
        @current-change="featuresCurrentChange"
        :current-page="featuresInfo.current_page"
        :page-sizes="[10, 20]"
        :page-size="featuresInfo.page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="featuresInfo.table_total">
      </el-pagination>
    </el-tab-pane>
    <!-- 新增提示框 -->
    <el-dialog class="newUserDialog" center width="600px" title="添加管理员" :visible.sync="newUserDialog">
      <!-- 用户名 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">账号</el-col>
        <el-col :span="12">
          <el-input class="newUserInput" @blur="checkUserName(newUserInfo.userName)" v-model="newUserInfo.userName" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 密码框 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">密码</el-col>
        <el-col :span="12">
          <el-input v-model="newUserInfo.pwd" class="newUserInput" placeholder="请输入内容" type="password">
          </el-input>
        </el-col>
      </el-row>
      <!-- 渠道名称 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">渠道名称</el-col>
        <el-col :span="12">
          <el-input v-model="newUserInfo.channelName" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 渠道号 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">渠道号</el-col>
        <el-col :span="12">
          <el-input v-model="newUserInfo.channelId" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>

      <!-- 页脚 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearNewUser">清 空</el-button>
        <el-button type="primary" @click="addNewUser">确 定</el-button>
      </div>
    </el-dialog>
  </el-tabs>
</template>

<script>
import {
  userAdmin,
  addNewUserAdmin, addNewChannelId
} from '@/utils/api'

export default {
  name: 'admin-system',
  data () {
    return {
      newUserDialog: false, // 新增用户显示弹窗
      featuresInfo: {
        data: [], // 功能选择表格数据
        page_size: 10, // 页面数据个数
        current_page: 1, // 当前 index
        table_total: 0 // 所有数据
      }, // 功能选择表格用到的数据
      newUserInfo: {
        userName: '',
        pwd: '',
        channelName: '',
        channelId: ''
      }
    }
  },
  mounted () {
    // 请求页面数据
    this.loadFeaturesData()
  },
  methods: {
    async addChannelId (index) {
      const item = this.featuresInfo.data[index]
      try {
        const { value } = await this.$prompt('请输入渠道ID', '新增', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        })

        // 发送请求
        const res = await addNewChannelId(item.pid, value)

        await this.$checkData(res.data)

        this.loadFeaturesData()
      } catch (e) {

      }
    },
    // 新增用户管理员
    async addNewUser () {
      try {
        const res = await addNewUserAdmin({
          loginName: this.newUserInfo.userName,
          password: this.newUserInfo.pwd,
          channelName: this.newUserInfo.channelName,
          channelId: this.newUserInfo.channelId
        })

        await this.$checkData(res.data)

        // 添加成功
        this.$message({
          showClose: true,
          message: '添加成功',
          type: 'success'
        })

        // 关闭弹窗
        this.newUserDialog = false

        // 清空数据
        this.clearNewUser()

        // 重新获取 data
        this.loadFeaturesData()
      } catch (e) {
        console.log(e)
      }
    },
    // 清空新增角色数据
    clearNewUser () {
      this.newUserInfo.userName = ''
      this.newUserInfo.pwd = ''
      this.newUserInfo.channelName = ''
      this.newUserInfo.channelId = ''
    },
    // 加载用户管理数据
    async loadFeaturesData () {
      try {
        // 发送 ajax 请求
        const res = await userAdmin(this.featuresInfo.current_page, this.featuresInfo.page_size)
        const data = await this.$checkData(res.data)
        // 所有数据个数
        this.featuresInfo.table_total = data.total
        // 清空 this.featuresInfo.data
        this.featuresInfo.data = []
        // 放置 data
        data.rows.forEach(v => {
          this.featuresInfo.data.push({
            userName: v.loginName,
            channelName: v.channelName,
            channelId: v.channelId,
            date: v.createTime,
            pid: v.id
          })
        })
      } catch (e) {
        console.log(e)
      }
    },
    // 分页数据
    featuresCurrentChange (index) {
      // 记录当前 page
      this.featuresInfo.current_page = index
      this.loadFeaturesData()
    },
    // 一页数据
    featuresSizeChange (index) {
      this.featuresInfo.page_size = index
      this.loadFeaturesData()
    },
    // 新增用户
    newUser () {
      this.newUserDialog = true
    }
  }
}
</script>
